// work  =>  sakht specialty va show anha
// faghat baray admin ghabel moshahede va use hastesh

import React, { memo, useEffect, useState } from "react";
// header site hastesh
import Header from "../../partials/Header";
// side bar site hastesh
import SideBar from "../Partials/SideBar";
// scroolbar custom hastes
import "react-perfect-scrollbar/dist/css/styles.css";
// baray link va reload site use shode
import { NavLink } from "react-router-dom";
// use jquery dar site
import $ from "jquery";
// use trasnlation dar site
import { useTranslation } from "react-i18next";
import Nouislider from "nouislider-react";
import "nouislider/distribute/nouislider.css";
// use materilize js and css
import M from "materialize-css";
// import action Add_specialty
import {
  Add_specialty,
  Get_all_specials,
  Delete_Specilty
} from "../../../../entity/Redux/Admin/Action";
// functionhai redux baray connect ba reducer va action
import { useSelector, useDispatch } from "react-redux";

import PropTypes from "prop-types";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableFooter from "@material-ui/core/TableFooter";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import IconButton from "@material-ui/core/IconButton";

const useStyles1 = makeStyles((theme) => ({
  root: {
    flexShrink: 0,
    marginLeft: theme.spacing(2.5),
  },
}));

export default memo(() => {
  const dispatch = useDispatch();
  const [specialtyContent, setSpecialtyContent] = useState({
    name : ''
  }); // state input name specialty
  const addSpeStatus = useSelector((state) => state.Admin.addSpecialty);
  const deleteStatus = useSelector(state => state.Admin.responseDel);
  const getAllSpecialty = useSelector((state) => state.Admin.getAllSpecialty);
  const { t, i18n } = useTranslation();
  useEffect(() => {
    dispatch(Get_all_specials());
    // check kardan response add_specialty
    if (addSpeStatus) {
      // if ok boud in etefagh byofte
      M.toast({ html: "Specialty added successfully!" });
      setTimeout(() => {
        // reload this page
        window.location.reload();
      }, 1500);
    }

    // if(deleteSpecilty)
    // {
    //     M.toast({hmtl : 'specialty successfully! please wait to reload page ...'});
    //     setTimeout(() => {
    //         //reload this page
    //         window.location.reload();
    //     },1500)
    // }

    // select mored estefade dar materilize
    var elems = document.querySelectorAll("select");
    M.FormSelect.init(elems);
  }, []);

  {
    /*

        -- language
        -- baraye modiriat do zabaneh kardan site
    */
  }

  function handleLang(e) {
    e.preventDefault();
    if (!localStorage.getItem("lan")) {
      localStorage.setItem("lan", "en");
    }

    var lan = localStorage.getItem("lan");
    var ckeckbox = $("input[name=cahngelan]");
    if (ckeckbox.is(":checked") && lan === "en") {
      localStorage.setItem("lan", "ch");
    } else {
      localStorage.setItem("lan", "en");
    }

    let lang = localStorage.getItem("lan");
    i18n.changeLanguage(lang);
  }

  //   end language

  // e => vouroudy hastesh ke ba tagheir input ersal mishe
  // in function jahat set data dar state be kar mire
  function specialtyChange(e) {

      setSpecialtyContent({
        ...specialtyContent,
        name: e.target.value,
      });
  }

  // e => inja baray stop kardan refresh page azash estefadeh mishe
  // in function baray submit form estefadeh mishe

  function submitForm(e) {
    e.preventDefault();
    console.log(specialtyContent.name);
    if (!specialtyContent.name)
    {
      M.toast({html : 'Enter specialty name!'})
    }else{
      dispatch(Add_specialty(specialtyContent)); // dispatch kardan action adding
    }
  }

  function deleteSpecilty(e, id) {
    dispatch(Delete_Specilty(id))
  }


  return (
    <div>
      <div id="loader-wrapper">
        <div id="loader"></div>
        <div className="loader-section section-left"></div>
        <div className="loader-section section-right"></div>
      </div>

      <Header />
      <div id="main">
        <div className="wrapper">
          <SideBar />

          <div id="back_shado"></div>
          <div className="container">
            <div className="row">
              <div className="col s12">
                <div id="breadcrumbs-wrapper">
                  <div className="container">
                    <div className="row">
                      <div className="col s10 m6 l6">
                        <h5 className="breadcrumbs-title">Specialty list</h5>
                        <ol className="breadcrumbs">
                          <li>
                            <NavLink to="/admin">
                              <a>Dashboard</a>
                            </NavLink>
                          </li>
                          <li>
                            <a className="active">Specialty list</a>
                          </li>
                        </ol>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col s12" style={{ marginBottom: 30 }}>
                <section id="content">
                  <div id="work-collections">
                    <div className="row">
                      <div className="col s12 m12 l6">
                        <ul
                          id="projects-collection"
                          className="collection z-depth-1"
                        >
                          <li className="collection-item avatar">
                            <i className="material-icons" id="custom_icon">
                              style
                            </i>
                            <h6 className="collection-header m-0">Specialty</h6>
                            <p>Add new Specialty</p>
                          </li>

                          <div id={"div-grade-add"}>
                            <form onSubmit={(e) => submitForm(e)}>
                              <div
                                className="input-field col s12"
                                style={{ marginTop: 0, position: "relative" }}
                              >
                                {/*  value marbout be input haman name specialty hastesh  */}
                                <p style={{ color: "#ccc" }}>
                                  &nbsp;&nbsp;Specialty Name
                                </p>
                                <div id="input_back">
                                  <input
                                    placeholder="Specialty name ..."
                                    autoComplete="false"
                                    name="name"
                                    onChange={(e) => specialtyChange(e)}
                                    type="text"
                                    className="validate"
                                  />
                                </div>
                              </div>

                              <div
                                className={"col s12"}
                                style={{ marginTop: 20 }}
                              >
                                {/* btn submit form */}
                                <input
                                  className={"btn waves-effect blue darken-4"}
                                  type={"submit"}
                                  style={{ textTransform: "unset" }}
                                  value={"Save"}
                                />
                              </div>
                            </form>
                          </div>
                        </ul>
                      </div>

                      <div className="col s12 m12 l6">
                        <ul
                          id="projects-collection"
                          className="collection z-depth-1"
                        >
                          <li className="collection-item avatar">
                            <i className="material-icons" id="custom_icon">
                              style
                            </i>
                            <h6 className="collection-header m-0">
                              Specialty(s)
                            </h6>
                            <p>Specialty's list</p>
                          </li>

                          <div id={"div-grade-add"}>
                            {getAllSpecialty.data
                              ? getAllSpecialty.data.data.map((item) => {
                                
                                  return (
                                    <ul id={"grade-list"}>
                                      <li>{item.name}</li>
                                      <li>
                                        <i
                                          onClick={(e) =>
                                            deleteSpecilty(e, item.id)
                                          }
                                          className={"material-icons"}
                                        >
                                          delete
                                        </i>
                                      </li>
                                    </ul>
                                  );
                                })
                              : ""}
                          </div>
                        </ul>
                      </div>
                    </div>
                  </div>
                </section>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
